@font-face {
  font-family: 'fontello';
  src: url(../public/assets/fonts/fontello.woff2) format('woff2'),
    url(../public/assets/fonts/fontello.woff) format('woff');
}
body {
  margin: 0;
  font-family: 'Barlow', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  width: 100%;
  @media (min-width: 162px) {
    width: 100%;
  }
  @media (min-width: 424px) {
    width: 100%;
    max-width: 428px;
  }
  @media (min-width: 768px) {
    width: 95%;
    max-width: unset;
  }
  @media (min-width: 992px) {
    max-width: 970px;
  }
  @media (min-width: 1200px) {
    max-width: 1280px;
  }
}
